body {
  font-family: Arial, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  background-image: url(https://wallpaperaccess.com/full/84248.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
}


.contentLayout {
  color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1.5rem 2rem;
  margin: 0 auto;

  line-height: 10rem;
  background-color: transparent;
}

h2 {
  text-align: center;
  font-family: Lobster Two;
  font-size: min(3rem, 6vw);
  color:#bdb2ad;
  margin: 0;
  padding: 2vw;
  padding-bottom: 100px;
}

.topnav {
  width: 500px;
  height: 40px;
  background: #444;
  background: rgba(0,0,0,.2);
  border-radius:  3px;
  border: 1px solid #fff;
  margin: 0 auto;
}

input[type=text] {
  width: 370px;
  padding: 10px 5px;
  float: left;
  color: #ccc;
  border: 0;
  background: transparent;
  border-radius: 3px 0 0 3px;
}

input[type=text]:focus {
  outline: 0;
  background:transparent;
}



.topnav button {
    position: relative;
    float: right;
    border: 0;
    padding: 0;
    cursor: pointer;
    height: 40px;
    width: 120px;
    color: #fff;
    background: transparent;
    border-left: 1px solid #fff;
    border-radius: 0 3px 3px 0;

}

.topnav button:hover {
  border: 1px solid lightgreen;
  border-radius: 5px;
}

footer {
  color: white;
  position: fixed;
  top: 90vh;
  left: 40vw;
  font-size: min(2rem, 3vw);
}


.temperature {
  opacity: 0;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 30px;
  width: 30rem;
  height: 16rem;
  border-style: groove;
  border-radius: 3px;
  border-width: 4px;
  border-color: black;
  margin: 0 auto;
  margin-top: 15rem;
  line-height: 10rem;
  background-color: lightgray;
}



.forecast-layout {
  color:white;
  font-size: 3rem;
  margin: 0 auto;
  padding-top: 30px;
  background-color: transparent;
  width: fit-content;
  height: 40vh;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.forecast-flex {
  padding-top: 7vh;
}

.flex-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-bottom: 1px;
}
